body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 20px;
  }
  
  h1, h2 {
    text-align: center;
  }

  h2 {
    text-align: center;
    margin-bottom: 30px;
  }
  
  .release-list {
    list-style-type: none;
    padding: 0;
  }
  
  .release-item {
    margin-bottom: 20px;
    border: 1px solid #ddd;
    padding: 20px;
    background-color: #f9f9f9;
  }
  
  @media (min-width: 768px) {
    .release-item {
      display: flex;
      align-items: center;
    }
    
    .release-item .version {
      flex-basis: 120px;
      font-weight: bold;
      margin-right: 10px;
    }
    
    .release-item .description {
      flex-grow: 1;
    }
  }